// pages/api/gpt/generate-era.ts
import type { NextApiRequest, NextApiResponse } from "next";
import OpenAI from "openai";

const openai = new OpenAI({ apiKey: process.env.OPENAI_API_KEY });

export default async function handler(req: NextApiRequest, res: NextApiResponse) {
  if (req.method !== "POST") return res.status(405).json({ error: "Method not allowed" });
  
  const { tracks } = req.body;
  if (!tracks || tracks.length === 0) return res.status(400).json({ error: "No tracks provided" });
  
  const songList = tracks.map((t: any) => `${t.name} by ${t.artists[0].name}`).join(", ");
  
  const prompt = `Based on these top songs: ${songList}, describe what kind of 'era' this person is currently in.`;
  
  const response = await openai.chat.completions.create({
    model: "gpt-4",
    messages: [{ role: "system", content: prompt }],
  });
  
  res.status(200).json({ era: response.choices[0].message.content });
}
